import React from 'react'
import ProjectCard from './ProjectCard'
import "./Projects.css"
import testimage from './assets/CGL.jpg'
import testimage2 from './assets/html-icon.png'
import testimage3 from './assets/long-wallpaper.jpg'

const Projects = () => {
const descExample = "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam."

    return (
        <div className="projectContainer">
            <ProjectCard imageSrc={testimage} projTitle={"Proj 1"} projDesc={descExample}></ProjectCard>
            <ProjectCard imageSrc={testimage2} projTitle={"Proj 1"} projDesc={"proj desc"}></ProjectCard>
            <ProjectCard imageSrc={testimage3} projTitle={"Proj 1"} projDesc={"proj desc"}></ProjectCard>
        </div>
    )
}

export default Projects
